<template>
    <LayoutBase :title="t('EventView.title')">
        <v-col>
            <v-row>
                <v-btn v-if="auth.canAdd(PermissionTarget.EVENT)" @click="createEvent">
                    <template #prepend>
                        <FontAwesomeIcon :icon="faPlus" />
                    </template>
                    {{ t("EventView.newEvent") }}
                </v-btn>
            </v-row>
        </v-col>
        <v-col>
            <v-row>
                <v-data-table-server
                    :key="`blog-table-${refreshKey}`"
                    v-model:items-per-page="perPage"
                    class="elevation-1 primary"
                    item-value="id"
                    density="compact"
                    :headers="headers"
                    :items="events"
                    :items-length="totalItems"
                    :loading="loading"
                    :page="currentPage"
                    :items-per-page-options="pageSizeOptions"
                    :no-data-text="t('EventView.noEventsFound')"
                    :loading-text="t('EventView.loadingEvents')"
                    @update:options="debouncedLoad"
                >
                    <template #item.archived="{ item }">
                        <FontAwesomeIcon v-if="item.archived" :icon="faCheck" class="text-green" />
                        <FontAwesomeIcon v-else :icon="faXmark" class="text-red" />
                    </template>
                    <template #item.date="{ item }">
                        {{ d(item.date, "long") }}
                    </template>
                    <template #item.actions="{ item }">
                        <v-btn
                            v-if="auth.canDelete(PermissionTarget.EVENT)"
                            density="compact"
                            variant="text"
                            color="red"
                            @click="deleteEvent(item)"
                        >
                            <template #prepend>
                                <FontAwesomeIcon :icon="faXmark" />
                            </template>
                            Delete
                        </v-btn>
                        <v-btn
                            v-if="auth.canChange(PermissionTarget.EVENT)"
                            density="compact"
                            variant="text"
                            @click="editEvent(item.id)"
                        >
                            <template #prepend>
                                <FontAwesomeIcon :icon="faPenToSquare" />
                            </template>
                            Edit
                        </v-btn>
                    </template>
                </v-data-table-server>
            </v-row>
        </v-col>
    </LayoutBase>
    <EventDialog ref="dialog" />
</template>

<script setup lang="ts">
import { faCheck, faPenToSquare, faPlus, faXmark } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";
import { debounce } from "lodash-es";
import { type Ref, inject, ref } from "vue";
import { useI18n } from "vue-i18n";
import { useToast } from "vue-toastification";
import type { VDataTable } from "vuetify/components";

import * as api from "@/api";
import type { Event } from "@/api";
import EventDialog from "@/components/EventDialog.vue";
import LayoutBase from "@/components/LayoutBase.vue";
import { PermissionTarget, useAuth } from "@/services/auth";
import { useEvents } from "@/services/events";
import { type LoadArgs, getLoadArgs } from "@/services/utils/query_tools";
import { confirmDialogKey } from "@/symbols";
import type { ConfirmDialogType } from "@/symbols";

type ReadonlyHeaders = VDataTable["$props"]["headers"];

const { t, d } = useI18n();

const dialog: Ref<InstanceType<typeof EventDialog> | undefined> = ref(undefined);
const confirmDialog: ConfirmDialogType = inject(confirmDialogKey)!;

const toast = useToast();
const eventService = useEvents();
const auth = useAuth();
const loading = ref(false);
const pageSizeOptions = [25, 50, 100];
const perPage = ref(pageSizeOptions[0]);
const totalItems = ref(0);
const currentPage = ref(1);
const events: Ref<Event[]> = ref([]);
const refreshKey = ref(0);
const headers: ReadonlyHeaders = [
    {
        title: t("EventView.headers.id"),
        sortable: true,
        key: "id",
    },
    {
        title: t("EventView.headers.name"),
        sortable: false,
        key: "name",
    },
    {
        title: t("EventView.headers.tag"),
        sortable: false,
        key: "tag",
    },
    {
        title: t("EventView.headers.date"),
        sortable: true,
        key: "date",
    },
    {
        title: t("EventView.headers.archived"),
        sortable: false,
        key: "archived",
    },
    {
        title: t("EventView.headers.mainUrl"),
        sortable: false,
        key: "mainurl",
    },
    {
        title: t("EventView.headers.actions"),
        sortable: false,
        key: "actions",
        align: "end",
    },
];

async function flushData() {
    refreshKey.value += 1;
    await eventService.refreshEvents();
}

async function load(args: LoadArgs) {
    loading.value = true;
    try {
        const response = await api.adminEventsList({ query: getLoadArgs(args) });
        events.value = response.data!.results;
        totalItems.value = response.data!.count;
    } catch (e) {
        toast.error(t("EventView.loadFailure"));
        console.error(e);
    } finally {
        loading.value = false;
    }
}

const debouncedLoad = debounce(load, 250); // Don't murderate the server API

async function deleteEvent(item: Event): Promise<void> {
    const text = t("EventView.confirmDelete", item);
    const ok = await confirmDialog.value!.confirm(text);
    if (ok) {
        try {
            await api.adminEventsDestroy({ path: { id: item.id } });
            await flushData();
            toast.success(t("EventView.deleteSuccess"));
        } catch (e) {
            toast.error(t("EventView.deleteFailure"));
            console.error(e);
        }
    }
}

async function editEvent(id: number): Promise<void> {
    const response = await api.adminEventsRetrieve({ path: { id } });
    const ok = await dialog.value!.modal(response.data!);
    if (ok) {
        await flushData();
    }
}

async function createEvent() {
    const ok = await dialog.value!.modal();
    if (ok) {
        currentPage.value = 1;
        await flushData();
    }
}
</script>
